.tomper h5,span{
    color: #f7f7f7;
    font-size: 24px;
    font-weight: 700;
}

.tomper h2,.tomper h5{
    text-align: center;
}

.tomper h2{
    font-size: 64px;
    color: #F2B821;
    padding-top: 20px;
}

.tomper{
    padding-top: 150px;
    align-self: center;
}

.cards{
    display: flex;
    gap: 10rem;
    align-self: center;
    padding-left: 15rem ;
    padding-top: 76px;
}

.card{
    width: 528px;
    height: 330px;
    background-color: #F2B821;
    position: relative;
    align-content: center;
    border-radius: 40px;
    box-shadow: 0px 4px 100px rgba(0, 0, 0, 0.25);
}

.card h5 {
    position: absolute;
    top: 151px;
    left: 151px;
    font-size: 24px;
}

.sec h5 {
    left: 179px;
}

.small{
    position: absolute;
    width: 42px;
    height: 42px;
    background: #2E2C2C;
    left: 24px;
    top: 23px;
    border-radius: 100%;
    box-shadow: inset 0px 5px 8px rgba(0, 0, 0, 0.5);
}

